import '../assets/css/montserrat-font.css'
const Register = () => {
    return ( 
        <div class="page-content">
		<div class="form-v10-content">
			<form class="form-detail" action="#" method="post" id="myform">
				<div class="form-left">
					<h2>General Infomation</h2>
					<div class="form-group">
						<div class="form-row form-row-1">
							<input type="text" name="first_name" id="first_name" class="input-text" placeholder="Prénom" required />
						</div>
						<div class="form-row form-row-2">
							<input type="text" name="last_name" id="last_name" class="input-text" placeholder="Nom" required />
						</div>
					</div>
					<div class="form-row">
						<input type="text" name="company" class="company" id="company" placeholder="Entreprise(optionnel)" required />
					</div>
					<div class="form-row">
						<input type="text" name="Email" id="Email" class="input-text" required pattern="[^@]+@[^@]+.[a-zA-Z]{2,6}" placeholder="Email" />
					</div>

				</div>
				<div class="form-right">
					<h2>Contact Details</h2>
					<div class="form-row">
						<input type="text" name="street" class="street" id="street" placeholder="Address" required />
					</div>

					<div class="form-row">
						<select name="country">
						    <option value="Ville">Country</option>
						    <option value="Vietnam">Vietnam</option>
						    <option value="Malaysia">Malaysia</option>
						    <option value="India">India</option>
						</select>
						<span class="select-btn">
						  	<i class="zmdi zmdi-chevron-down"></i>
						</span>
					</div>
					<div class="form-group">
						<div class="form-row form-row-1">
							<input type="text" name="code" class="code" id="code" placeholder="Code +" required />
						</div>
						<div class="form-row form-row-2">
							<input type="text" name="phone" class="phone" id="phone" placeholder="Tel" required />
						</div>
					</div>
					<div class="form-checkbox">
						<label class="container"><p>J’accepte <a href="#" class="text">les termes et conditions</a> de votre site.</p>
						  	<input type="checkbox" name="checkbox" />
						  	<span class="checkmark"></span>
						</label>
					</div>
					<div class="form-row-last">
						<input type="submit" name="register" class="register" value="Inscrire" />
					</div>
				</div>
			</form>
		</div>
	</div>
     );
}
 
export default Register;